        "Jestem tutaj, Å¼eby uÅ‚atwiÄ‡ Ci zrozumienie systemu emerytalnego. W czym mogÄ™ pomÃ³c?",
        "KaÅ¼da zÅ‚otÃ³wka skÅ‚adek to inwestycja w TwojÄ… przyszÅ‚oÅ›Ä‡! SprawdÅºmy razem Twoje konto ZUS?",
